<template>
  <v-container>
    <v-data-table
      :headers="tableHeaders"
      :items="basketItems"
      item-value="id"
      class="elevation-1 mt-0"
    >
      <template #top>
        <v-toolbar flat>
          <v-toolbar-title>Basket</v-toolbar-title>
        </v-toolbar>
      </template>

      <template #item.quantity="{ item }">
        <v-btn
          icon="mdi-plus"
          variant="text"
          @click="changeQuantity(item, 'add')"
        />
        <v-chip label>{{ item.quantity }}</v-chip>
        <v-btn
          icon="mdi-minus"
          variant="text"
          @click="changeQuantity(item, 'remove')"
        />
      </template>

      <template #item.price="{ item }">
        <v-chip color="secondary">${{ item.price }}</v-chip>
      </template>

      <template #item.total="{ item }">
        <v-chip color="primary">${{ getTotalPriceForItem(item) }}</v-chip>
      </template>

      <template #item.actions="{ item }">
        <v-btn
          icon="mdi-trash-can-outline"
          color="error"
          variant="text"
          @click="removeFromBasket(item)"
        ></v-btn>
      </template>

      <template #body.append="{ columns }">
        <tr class="v-data-table__tr">
          <td v-for="(column, idx) in columns" :key="column.text">
            <div v-if="idx === 0">
              <strong>Total</strong>
            </div>

            <v-chip v-if="column.key === 'total'" color="primary" label>
              ${{ totalPrice }}
            </v-chip>
          </td>
        </tr>
      </template>
    </v-data-table>
  </v-container>
</template>

<script>
import { ref, computed } from "vue";

import { useOrdersStore } from "@/stores/ordersStore";

export default {
  name: "OrdersTable",
  setup() {
    const basketItems = computed(() => useOrdersStore().basket);

    const tableHeaders = ref([
      { title: "Product", value: "title" },
      { title: "Store", value: "vendorName" },
      { title: "Quantity", value: "quantity" },
      { title: "Price", value: "price" },
      { title: "Total Price", value: "total" },
      { title: "", value: "actions" },
    ]);

    const removeFromBasket = (item) => {
      useOrdersStore().removeFromBasket(item.id);
    };

    const totalPrice = computed(() => {
      return basketItems.value.reduce(
        (acc, item) => acc + getTotalPriceForItem(item),
        0
      );
    });

    const getTotalPriceForItem = (item) => {
      return item.price * (item?.quantity || 1);
    };

    const changeQuantity = (item, action = "add") => {
      const { quantity, id } = item;
      const newQuantity = action === "add" ? quantity + 1 : quantity - 1;
      if (newQuantity < 1) return;
      useOrdersStore().updateProductQuantity(id, newQuantity);
    };

    return {
      basketItems,
      tableHeaders,
      totalPrice,
      removeFromBasket,
      changeQuantity,
      getTotalPriceForItem,
    };
  },
};
</script>

<style scoped>
.v-data-table {
  margin-top: 20px;
}
</style>
